@import "./../../components/colors";
.header,
.footer {
    display: none;
}

.section-account {
    padding-top: 70px;
    padding-bottom: 70px;
    @media (max-width: 767px) {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    &__title {
        font-family: 'Normalidad';
        font-weight: bold;
        font-size: 35px;
        line-height: 140%;
        letter-spacing: 0.02em;
        word-spacing: 100vw;
        text-transform: uppercase;
        margin-bottom: 50px;
        @media (max-width: 767px) {
            font-size: 19px;
            margin-bottom: 35px;
        }
        &-small {
            font-weight: bold;
            font-size: 17px;
            line-height: 152%;
            letter-spacing: -0.01em;
            color: $c-222222;
            text-transform: unset;
            margin-bottom: 29px;
            @media (max-width: 767px) {
                font-size: 15px;
                line-height: 140%;
                letter-spacing: 0.04em;
                margin-bottom: 25px;
            }
        }
    }
    &__uplink {
        margin-bottom: 50px;
        @media (max-width: 767px) {
            margin-bottom: 45px;
        }
    }
    &__description {
        display: flex;
        flex-direction: column;
    }
    &__score {
        display: flex;
        flex-direction: column;
    }
    &__nominations {
        position: relative;
        & .swiper-container {
            display: flex;
            flex-direction: column;
            & .swiper-slide {
                & .mechanics-card {
                    margin-right: 0;
                }
            }
        }
    }
}